import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'UbuntuSwap',
  description:
    'The most popular AMM on BSC by user count! Earn CHY through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens on a platform you can trust.',
  image: 'https://greenhouseswap.finance/images/hero.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  switch (path) {
    case '/':
      return {
        title: `${t('Home')} | ${t('UbuntuSwap')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('UbuntuSwap')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('UbuntuSwap')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('UbuntuSwap')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('UbuntuSwap')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('UbuntuSwap')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('UbuntuSwap')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('UbuntuSwap')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('UbuntuSwap')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('UbuntuSwap')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('UbuntuSwap')}`,
      }
    default:
      return null
  }
}
